<template>
	<div>
		<ul class="g_formList">
			<li class="formList_li"><span class="character"><b>*</b> 姓 名</span><input class="text" type="text" placeholder="请输入姓名" v-model="user.name" ></li>
			<li class="formList_li"><span class="character"><b>*</b> 身份证号</span><input class="text" type="text" placeholder="请输入身份证" v-model="user.identityId"></li>
			<li class="formList_li">
				<span class="character"><b>*</b> 户籍所在地</span>
				<select class="text" name="" v-model="user.nativePlace.regionInfo">
					<option v-for="item in listmessageregionInfo" v-bind:value="item.value">{{item.text}}</option>
				</select>
			</li>
			<li class="formList_li">
				<span class="character"><b>&nbsp;</b> 户口性质</span>
				<select class="text" name="" v-model="user.agriculturalAccount">
					<option v-for="item in listagric" v-bind:value="item.value">{{item.text}}</option>
				</select>
			</li>
			<li class="formList_li"><span class="character"><b>*</b> 联系电话</span><input class="text" type="text" placeholder="家庭或办公室电话" v-model="user.telephone"></li>
			<li class="formList_li"><span class="character"><b>*</b> 手机号码</span><input class="text" type="text" placeholder="请输入手机号码" v-model="user.mobile"></li>
			<li class="formList_li"><span class="character"><b>*</b> 常用QQ</span><input class="text" type="text" placeholder="请输入QQ号码" v-model="user.qq"></li>
			<li class="formList_li"><span class="character"><b>*</b> 联系邮箱</span><input class="text" type="text" placeholder="请输入邮箱" v-model="user.email"></li>
			<li class="hint"><p>该邮箱地址为后续接收电子版材料地址，请务必填写准确。</p></li>
			<li class="formList_li">
				<span class="character"><b>*</b> 联系地址</span>
				<select class="text" name="" v-model="user.address.regionInfo">
					<option v-for="item in listmessageaddressregionInfo" v-bind:value="item.value">{{item.text}}</option>
				</select>
			</li>
			<li class="formList_li"><span class="character"><b></b></span><input class="text" type="text" placeholder="具体地址（街道，号）" v-model="user.address.street"></li>
			<li class="formList_li"><span class="character"><b>*</b> 邮 政 编 码</span><input class="text" type="text" placeholder="请输入邮编" v-model="user.postCode"></li>
			<li class="hint"><p>该地址为默认接收快递地址，请务必填写准确。</p></li>
			
		</ul>
		
		
		<div v-if="btns" class="button_lack">保存并返回</div>
		<div v-else class="button_whole" @click="UserBaseInfoSubmit()">保存并返回</div>
		
		
	</div>
</template>

<script>
	
	
	export default{
		
		data(){
			return {
				btns:true,
				keep:true,
				listmessageregionInfo:[
					{ text: '请选择', value: 0 },
				    { text: '广东广州', value: 1 },
				    { text: '广东深圳', value: 2 }
				],
				listagric:[
					{ text: '请选择', value: 0 },
				    { text: '非农业', value: 1 },
				    { text: '农业', value: 2 }
				],
				listmessageaddressregionInfo:[
					{ text: '请选择', value: 0 },
				    { text: '广东广州', value: 1 },
				    { text: '广东深圳', value: 2 }
				],
				user:{
		         	'name':"",
					'identityId':"",
					'nativePlace':{
						'regionInfo':""
					},
					'agriculturalAccount':0,
					'email':"",
					'address':{
						'regionInfo':"",
						'street':""
					},
					'telephone':"",
					'mobile':"",
					'qq':"",
					'postCode':"",
				}
			}
		},
		methods:{
			UserBaseInfo(){
				this.$Api.UserBaseInfo(this.$store.state.sessionId).then((res)=>{
					this.user.name = res.q.user.name;//名字
					this.user.identityId = res.q.user.identityId;//身份证号码
					this.user.nativePlace.regionInfo = res.q.user.nativePlace.regionInfo;//户籍所在地
					this.user.agriculturalAccount = res.q.user.agriculturalAccount;//户口性质
					this.user.email = res.q.user.email;//邮箱地址
					this.user.address.regionInfo = res.q.user.address.regionInfo;
					this.user.address.street = res.q.user.address.street;
					this.user.telephone = res.q.user.telephone;//电话号码
					this.user.mobile = res.q.user.mobile;//手机号码
					this.user.qq = res.q.user.qq;//QQ号码
					this.user.postCode = res.q.user.postCode//邮政编码
				})
			},
			UserBaseInfoSubmit(){
				
				this.user = {
			         	'name':this.user.name,
						'identityId':this.user.identityId,
						'nativePlace':{
							'regionInfo':this.user.nativePlace.regionInfo
						},
						'agriculturalAccount':this.user.agriculturalAccount,
						'email':this.user.email,
						'address':{
							'regionInfo':this.user.address.regionInfo,
							'street':this.user.address.street
						},
						'telephone':this.user.telephone,
						'mobile':this.user.mobile,
						'qq':this.user.qq,
						'postCode':this.user.postCode,
				},
				this.$Api.UserBaseInfoSubmit(this.$store.state.sessionId,this.user).then((res)=>{
					this.$toast.center('保存成功');
					 setTimeout(() => { 
			            this.$router.go(-1);
			        }, 1000)
				})
			},
//			RegionList(){
//				this.$Api.RegionList(this.$store.state.sessionId).then((res)=>{
//					console.log(res)
//				})
//			}
			
			getAnswer(){
				if(this.user.name!=null&&this.user.name!=""&&this.user.identityId!=null&&this.user.identityId!=""&&this.user.nativePlace.regionInfo!=null&&this.user.nativePlace.regionInfo!=""&&this.user.email!=null&&this.user.email!=""&&this.user.address.regionInfo!=null&&this.user.address.regionInfo!=""&&this.user.address.street!=null&&this.user.address.street!=""&&this.user.telephone!=null&&this.user.telephone!=""&&this.user.mobile!=null&&this.user.mobile!=""&&this.user.qq!=null&&this.user.qq!=""&&this.user.postCode!=null&&this.user.postCode!=""){
					this.btns = false;
				}else{
					this.btns = true;
				}
			}
		},
		mounted(){
	        this.UserBaseInfo();
//	        this.RegionList();
	   },
	   watch:{
			user:{
				handler(val, oldVal){
				this.getAnswer()
				},
				 deep:true
			}
        },

	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	
	
	input[type=text], input[type=password], input[type=submit], select { border: #cccccc 1px solid; height: .8rem; border-radius: .1rem; width: 100%; }
	input[type=text]:focus, input[type=password]:focus, input[type=submit]:focus, select:focus { border-color: #fa8a12; }
	input[type=text], input[type=password] { padding: 0 .1rem; }
	input[type=submit] { background-color: #fa8a12; text-align: center; width: 100%; }
	textarea { height: auto; min-height: 1.2rem; border: #cccccc 1px solid; border-radius: .1rem; padding: .2rem .3rem; width: 100%; }
	input:-moz-placeholder { color: #999999; }
	::-webkit-input-placeholder { color: #999999; }
	input[type=text], input[type=password] { -webkit-appearance: none; }
	
	.g_formList{padding: .3rem 0.3rem 0;
		.formList_li{padding-left: 2.3rem; position: relative; height: .7rem; margin-bottom: .3rem; font-size: .3rem; line-height: .7rem;
			.character{position: absolute; left: 0;color: #1a1a1a;font-weight: 600;
				b{color: #f32929;}
			}
			.text{height: .7rem;padding:0 0.1rem;width: 4rem;}
		}
		.hint{margin-bottom: .3rem;
			p{font-size: .25rem;color: #fa8a12;padding:0 0.4rem 0 0.18rem;}
		}
	}
	.button_lack{width: 6.9rem;height: 1rem;background-color: #ccc;font-size: 0.34rem;color: #FFFFFF;text-align: center;line-height: 1rem;border-radius:0.5rem ;margin:0.5rem auto 1.4rem;}
	.button_whole{width: 6.9rem;height: 1rem;background-color: #fa8a12;font-size: 0.34rem;color: #FFFFFF;text-align: center;line-height: 1rem;border-radius:0.5rem ;margin:0.5rem auto 1.4rem;}
	
</style>


